import { authConstants } from '../constants';
import Axios from 'axios';

export const authActions = {
    auth
}

function auth(user) {
    return dispatch => {
        dispatch(request());

        Axios.post('http://localhost:8000/v1/api/user/authenticate', user)
            .then(res => {
                console.log(res);
                dispatch(success(res.data))
            })
            .catch(err => {
                console.log(err.response.data.message);
                dispatch(failure(err.response.data.message))
            });
    }

    function request() { return { type: authConstants.AUTH_REQUEST } }
    function success(user) { return { type: authConstants.AUTH_SUCCESS, user } }
    function failure(error) { return { type: authConstants.AUTH_FAILURE, error } }
}