import React, { Component } from 'react';


import Feed from "./Feed/Feed";

import '../Featured/Featured.css'

class Feeds extends Component {
    render() {
        return (
            <div className="mt-5">
                <div className="section-title">
                    <h2><span>All Stories</span></h2>
                </div>
                <div className="row">
                    <Feed />
                    <Feed />
                    <Feed />
                    <Feed />
                </div>
            </div>
        );
    };
}

export default Feeds;