import React, { Component } from 'react';
import RichTextEditor from 'react-rte';
import ImgButton from '../../components/UI/Button/ImgButton';
import Image from '../../components/UI/Image/Image';
import Button from '../../components/UI/Button/Button';
import Spinner from '../../components/UI/Spinner/Snipper';

import './New.css';

class NewPost extends Component {
    state = {
        value: RichTextEditor.createEmptyValue(),
        images: [],
        loading: false
    }

    onChange = (value) => {
        this.setState({ value });
    }

    onImgChange = e => {
        const files = Array.from(e.target.files);

        const formData = new FormData();

        files.forEach((file, i) => {
            formData.append(i, file);
        });

        this.setState({
            images: files
        });

        // for (var pair of formData.entries()) {
        //     console.log(pair);
        // }

        console.log(formData);
    }

    onPostSubmit = (e) => {
        e.preventDefault();
        this.setState({ loading: true });

        setTimeout(() => {
            this.setState({ loading: false });
        }, 3000);
    }



    render() {
        const { loading, images } = this.state;

        let btnTxt = !loading ? 'Submit' : <Spinner />;
        let btnStatus = !loading ? null : 'disabled';

        return (
            <div className="container mt-2">
                <div className="post-form">
                    <form onSubmit={this.onPostSubmit}>
                        <h1 className="h3 mb-3 font-weight-lighter text-center" style={{ color: '#f38120' }}>New Story</h1>
                        <div className="form-group">
                            <input type="text" className="form-control title" placeholder="Title" autoFocus />
                            <input type="text" className="form-control category" placeholder="Category" />
                            <input type="text" className="form-control tags" placeholder="Tags" />
                        </div>
                        <div className="form-group">
                            <ImgButton imgChange={this.onImgChange} />
                            <Image images={images} />
                        </div>
                        <div className="form-group">
                            <RichTextEditor
                                value={this.state.value}
                                onChange={this.onChange}
                                placeholder="Add Your Story"
                            />
                        </div>
                        <Button
                            btnType="btn-info"
                            btnSize="btn-lg"
                            btnBlock="btn-block"
                            btnDisabled={btnStatus}>{btnTxt}</Button>
                    </form>
                </div>
            </div>
        );
    }
}

export default NewPost;