import React, { Component } from "react";
import { connect } from 'react-redux';
import Button from '../../components/UI/Button/Button';
import Spinner from "../../components/UI/Spinner/Snipper";

import { userActions } from '../../store/actions';

import './Signup.css'

class Signup extends Component {
    state = {
        user: {
            username: '',
            email: '',
            password: ''
        },
        submitted: false
    };

    handleChange = (e) => {
        const { name, value } = e.target;
        const { user } = this.state;

        this.setState({
            user: {
                ...user,
                [name]: value
            }
        });
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.setState({ submitted: true });

        const { user } = this.state;
        if (user.username && user.email && user.password) {
            this.props.onRegister(user);
        }
    }

    render() {
        const { user, submitted } = this.state;

        let btnTxt = !this.props.loading ? "Submit" : <Spinner />;
        let btnStatus = !this.props.loading ? null : "disabled";

        return (
            <div className="wrapper">
                <form className="form mt-2" onSubmit={this.handleSubmit}>
                    <h1 className="h3 mb-3 font-weight-lighter text-center" style={{ color: '#f38120' }}>Sign Up</h1>
                    {this.props.isError &&
                        <div className="alert alert-danger" role="alert">
                            {this.props.error}
                        </div>
                    }

                    {submitted && (!user.username || !user.email || !user.password) &&
                        <div className="alert alert-danger" role="alert">
                            All field are required!
                        </div>
                    }

                    <label htmlFor="inputUsername" className="sr-only">Username</label>
                    <input
                        type="text"
                        id="inputUsername"
                        className="form-control"
                        placeholder="Username"
                        autoFocus
                        name="username"
                        value={user.username}
                        onChange={this.handleChange} />
                    <label htmlFor="inputEmail" className="sr-only">Email address</label>
                    <input
                        type="email"
                        id="inputEmail"
                        className="form-control"
                        placeholder="Email address"
                        name="email"
                        value={user.email}
                        onChange={this.handleChange} />
                    <label htmlFor="inputPassword" className="sr-only">Password</label>
                    <input
                        type="password"
                        id="inputPassword"
                        className="form-control"
                        placeholder="Password"
                        name="password"
                        value={user.password}
                        onChange={this.handleChange} />
                    <Button
                        btnType="btn-info"
                        btnSize="btn-lg"
                        btnBlock="btn-block"
                        btnStatus={btnStatus}>
                        {btnTxt}</Button>
                </form>
            </div>
        );
    };
}

const mapStateToProps = state => {
    return {
        user: state.userReducer.user,
        loading: state.userReducer.loading,
        error: state.userReducer.error,
        isError: state.userReducer.isError
    };
}

const mapDispatchToProps = dispatch => {
    return {
        onRegister: (user) => dispatch(userActions.register(user))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Signup);