import React, { Component } from 'react';

import './Layout.css';
import Aux from '../../hoc/Aux';
import Header from '../Header/Header';
import Footer from '../Footer/Footer';

class Layout extends Component {
    render() {
        return (
            <Aux>
                <Header />
                <main className="content">
                    {this.props.children}
                </main>
                <Footer />
            </Aux>
        )
    }
}

export default Layout;