import React from 'react';

import './Banner.css';

const banner = (props) => (
    <div className="banner">
        <div className="jumbotron rounded-0" style={{ backgroundColor: props.bgColor, color: props.color }}>
            <div className="container">
                <h1 className="display-4 text-center" style={{ fontFamily: 'Posteeze' }}>Posteeze</h1>
                <p className="lead text-center">A place to share your knowledge.</p>
            </div>
        </div>
    </div>
);

export default banner;