import { authConstants } from '../constants';

const initialState = {
    auth: [],
    error: '',
    isError: false,
    loading: false,
};

export function authReducer(state = initialState, action) {
    switch (action.type) {
        case authConstants.AUTH_REQUEST:
            return {
                loading: true
            }
        case authConstants.AUTH_SUCCESS:
            return {
                ...state,
                auth: action.user,
                loading: false
            }
        case authConstants.AUTH_FAILURE:
            return {
                ...state,
                error: action.error,
                isError: true,
                loading: false
            }
        default:
            return state
    }
}