import React, { Component } from 'react';
import { connect } from "react-redux";
import { authActions } from '../../store/actions'

import Button from '../../components/UI/Button/Button';
import Spinner from "../../components/UI/Spinner/Snipper";

class Signin extends Component {
    state = {
        user: {
            username: '',
            password: ''
        },
        submitted: false
    }

    handleChange = (e) => {
        const { name, value } = e.target;
        const { user } = this.state;

        this.setState({
            user: {
                ...user,
                [name]: value
            }
        })
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.setState({ submitted: true });

        const { user } = this.state;
        if (user.username && user.password) {
            this.props.onSignin(user);
        }
    }

    render() {
        const { user } = this.state;

        let btnTxt = !this.props.loading ? "Submit" : <Spinner />;
        let btnStatus = !this.props.loading ? null : "disabled";

        return (
            <div className="wrapper">
                <form className="form mt-2" onSubmit={this.handleSubmit}>
                    <h1 className="h3 mb-3 font-weight-lighter text-center" style={{ color: '#f38120' }}>Sign In</h1>
                    {this.props.isError &&
                        <div className="alert alert-danger" role="alert">
                            {this.props.error}
                        </div>
                    }
                    <label htmlFor="inputusername" className="sr-only">Username</label>
                    <input
                        type="text"
                        id="inputUsername"
                        className="form-control"
                        placeholder="Username"
                        style={style}
                        autoFocus
                        name="username"
                        value={user.username}
                        onChange={this.handleChange} />
                    <label htmlFor="inputPassword" className="sr-only">Password</label>
                    <input
                        type="password"
                        id="inputPassword"
                        className="form-control"
                        placeholder="Password"
                        name="password"
                        value={user.password}
                        onChange={this.handleChange} />
                    <Button
                        btnType="btn-info"
                        btnSize="btn-lg"
                        btnBlock="btn-block"
                        btnStatus={btnStatus}>{btnTxt}</Button>
                </form>
            </div>
        );
    };
}

const style = {
    borderTopLeftRadius: '0.25rem',
    borderTopRightRadius: '0.25rem'
}

const mapStateToProps = state => {
    return {
        auth: state.authReducer.auth,
        loading: state.authReducer.loading,
        isError: state.authReducer.isError,
        error: state.authReducer.error
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onSignin: (user) => dispatch(authActions.auth(user))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Signin);