import { userConstants } from "../constants";

const initialState = {
    user: [],
    error: '',
    isError: false,
    loading: false
};

export function userReducer(state = initialState, action) {
    switch (action.type) {
        case userConstants.REGISTER_REQUEST:
            return {
                loading: true
            }
        case userConstants.REGISTER_SUCCESS:
            return {
                ...state,
                user: action.user,
                loading: false,
            }
        case userConstants.REGISTER_FAILURE:
            return {
                error: action.error,
                isError: true,
                loading: false
            }
        default:
            return state
    }
}
